@font-face {
    font-family: 'netflix';
    src: url('../public/fonts/NetflixSans_W_Bd.woff2') format('woff2');
    font-weight: bold;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'netflix';
    src: url('../public/fonts/NetflixSans_W_Md.woff2') format('woff2');
    font-weight: 500;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'netflix';
    src: url('../public/fonts/NetflixSans_W_Rg.woff2') format('woff2');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    background-color: #141414;
    color: #FFFFFF;
    font-family: 'netflix', sans-serif;
}

button {
    cursor: pointer;
}

main {
    transform: translateY(-150px)
}

.movie-list-container {
    margin-left: 30px;
    margin-bottom: 40px;
    overflow-x: scroll;
    overflow-y: hidden;
    scrollbar-width: none; /* para firefox */
    -ms-overflow-style: none; /* para edge */
}

.movie-list-container::-webkit-scrollbar {
    display: none; /* para chrome */
}

footer {
    text-align: center;
    background-color: #000000;
}

.loading {
    position: absolute;
    width: 100%;
    height: 100vh;
    background-image: url('./assets/images/loading.webp');
    background-size: cover;
    background-position: center;
    z-index: 99;
}