.users-container {
    margin-top: 30vh;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.users-container * {
    color: rgb(128, 128, 128);
}

.users-container h1{
    color: rgb(255, 255, 255);
    text-align: center;
    font-size: 50px;
    font-weight: 100;
    margin-bottom: 30px;
}

.user-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    gap: 30px;
}

.user-container a {
    text-decoration: none;
}

.user-container p {
    text-align: center;
    font-size: 20px;
}

.avatar-container {
    max-width: 160px;
}

.avatar-container img {
    width: 100%;
    border-radius: 5px;
    border: 4px solid transparent;
}

.avatar-container:hover img{
    border: 4px solid #FFFFFF;
}

.avatar-container:hover p{
    color: #FFFFFF;
}

.users-container button {
    background-color: inherit;
    border: 1px solid gray;
    text-align: center;
    padding: 10px 30px;
    font-size: 18px;
    margin-top: 60px;
}

.users-container button:hover {
    border: 1px solid #FFFFFF;
    color: #FFFFFF;
}

@media(max-width: 768px) {
    .avatar-container {
        max-width: 120px;
    }
    .users-container h1{
        font-size: 40px;
        font-weight: 100;
    }
    .users-container {
        margin-top: 10vh;
    }
}